function getDifficultyColor(difficulty: string) {
  switch (difficulty) {
    case "Beginner":
      return "bg-green-500/10 text-green-600";
    case "Intermediate":
      return "bg-yellow-500/10 text-yellow-600";
    case "Advanced":
      return "bg-red-500/10 text-red-600";
    default:
      return "bg-gray-500/10 text-gray-600";
  }
}

